    -moz-appearance: none;
         appearance: none;
    outline: 0;
    font-size: 0;
    border: 1px solid #d1d1d1;
    background-color: #fff;
    border-radius: 3px;
    width: 13px;
    height: 13px;
    position: relative;
    vertical-align: 0;
    top: 2px
}
.weui-agree__checkbox:checked:before {
    font-family: weui;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    text-align: center;
    speak: none;
    display: inline-block;
    vertical-align: middle;
    text-decoration: inherit;
    content: "\EA08";
    color: #09bb07;
    font-size: 13px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -48%) scale(.73);
    transform: translate(-50%, -48%) scale(.73)
}
.weui-agree__checkbox:disabled {
    background-color: #e1e1e1
}
.weui-agree__checkbox:disabled:before {
    color: #adadad
}
