@-webkit-keyframes e {
0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg)
}
to {
        -webkit-transform: rotate(1turn);
        transform: rotate(1turn)
}
}
@keyframes e {
0% {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg)
}
to {
        -webkit-transform: rotate(1turn);
        transform: rotate(1turn)
}
}
.weui-slider {
    padding: 15px 18px;
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none
}
.weui-slider__inner {
    position: relative;
    height: 2px;
    background-color: #e9e9e9
}
.weui-slider__track {
    height: 2px;
    background-color: #1aad19;
    width: 0
}
.weui-slider__handler {
    position: absolute;
    left: 0;
    top: 50%;
    width: 28px;
    height: 28px;
    margin-left: -14px;
    margin-top: -14px;
    border-radius: 50%;
    background-color: #fff;
    -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, .2);
            box-shadow: 0 0 4px rgba(0, 0, 0, .2)
}
.weui-slider-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
        align-items: center
}
.weui-slider-box .weui-slider {
    -webkit-box-flex: 1;
    -ms-flex: 1;
        flex: 1
}
.weui-slider-box__value {
    margin-left: .5em;
    min-width: 24px;
    color: #888;
    text-align: center;
    font-size: 14px
}
